import { CommonTools } from "tools/commontools";

export class CommentDto {
    id:string;
    idpost: string;
    content: string;

    constructor(idpost: string, content?: string) {
        this.id = CommonTools.generateRandomString(10);
        this.idpost = idpost;
        this.content = content || '';
    }

    static filterByIdPost(comments: Array<CommentDto>, idpost: string): Array<CommentDto> {
        return comments.filter((item) => item.idpost === idpost);
    }
}